@use "./_burger-button.scss" as *;

.cb-menu-box {
    display: none;
}
.cb-menu-backdrop {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 197;
    background: rgba(0, 0, 0, 0.5);
}
.cb-menu-fill {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 199;
    height: 100vh;
    transform-origin: right center;
    background: #fff;
}
@media (min-width: 768px), (orientation: landscape) {
    .cb-menu-fill {
        width: 689px;
        left: auto;
    }
}
@media (min-width: 1600px) and (min-height: 700px) {
    .cb-menu-fill {
        width: 880px;
    }
}
.cb-menu-content {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 200;
    height: 100vh;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    padding: 100px 0 90px 0;
}
@media (min-height: 550px) {
    .cb-menu-content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 0;
    }
}
@media (min-width: 768px), (orientation: landscape) {
    .cb-menu-content {
        width: 689px;
        left: auto;
    }
}
@media (min-width: 768px) and (min-height: 550px) {
    .cb-menu-content {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 20px 0 0 0;
    }
}
@media (min-width: 768px) and (min-height: 600px) {
    .cb-menu-content {
        padding: 40px 0 0 0;
    }
}
@media (min-width: 1600px) and (min-height: 700px) {
    .cb-menu-content {
        width: 880px;
        padding: 50px 0 0 0;
    }
}
.cb-menu-container {
    width: 100%;
    padding: 0 30px 0 45px;
}
@media (min-width: 768px), (orientation: landscape) {
    .cb-menu-container {
        padding: 0 98px;
    }
}
@media (min-width: 768px) and (min-height: 550px) {
    .cb-menu-body {
        display: flex;
        align-items: center;
        height: 100%;
        width: 100%;
    }
}
.cb-menu-footer {
    padding: 50px 0 0 0;
}
@media (min-width: 768px) and (min-height: 550px) {
    .cb-menu-footer {
        padding: 0 0 50px 0;
    }
}
@media (min-width: 1600px) and (min-height: 700px) {
    .cb-menu-footer {
        padding: 0 0 84px 0;
    }
}
@media (min-width: 768px) and (min-height: 550px) {
    .cb-menu-grid {
        display: flex;
        width: 100%;
    }
}
@media (min-width: 768px) and (min-height: 550px) {
    .cb-menu-grid-col {
        flex: 1.2;
    }
}
.cb-menu-grid-col.-left {
    display: none;
}
@media (min-width: 768px) and (min-height: 550px) {
    .cb-menu-grid-col.-left {
        display: block;
        flex: 0.8;
    }
}
.cb-menu-title {
    display: none;
    padding: 0 0 30px 0;
    color: rgba(0, 0, 0, 0.3);
    font-size: 16px;
    line-height: 100%;
}
@media (min-height: 550px) {
    .cb-menu-title {
        display: block;
    }
}
@media (min-width: 1600px) and (min-height: 700px) {
    .cb-menu-title {
        padding: 0 0 50px 0;
    }
}
.cb-menu-title.-sm {
    padding: 0 0 12px 0;
}
@media (min-width: 1600px) and (min-height: 700px) {
    .cb-menu-title.-sm {
        padding: 0 0 20px 0;
    }
}
.cb-menu-navs {
    margin: -4px 0;
}
@media (min-width: 1600px) and (min-height: 700px) {
    .cb-menu-navs {
        margin: -9px 0;
    }
}
.cb-menu-nav {
    position: relative;
    display: block;
    padding: 4px 0;
}
@media (min-width: 1600px) and (min-height: 700px) {
    .cb-menu-nav {
        padding: 9px 0;
    }
}
.cb-menu-nav a {
    display: inline-block;
    position: relative;
    font-size: 38px;
    font-weight: 400;
    letter-spacing: -0.05em;
    line-height: 100%;
    text-decoration: none;
}
@media (min-width: 360px) {
    .cb-menu-nav a {
        font-size: 42px;
    }
}
@media (min-width: 768px) and (min-height: 550px) {
    .cb-menu-nav a {
        font-size: 56px;
    }
}
@media (min-width: 1600px) and (min-height: 700px) {
    .cb-menu-nav a {
        font-size: 60px;
    }
}
.cb-menu-nav a:before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: -2px;
    height: 1px;
    transform: scaleX(0);
    transform-origin: right center;
    background: currentColor;
    transition: transform 0.5s, transform-origin 0s;
}
.cb-menu-nav a em {
    display: block;
    position: relative;
    overflow: hidden;
    padding: 0 0.01em 0.04em 0;
}
.cb-menu-nav a span {
    display: inline-block;
    color: transparent;
    transition: transform 1.2s cubic-bezier(0.19, 1, 0.22, 1);
}
.cb-menu-nav a span:after,
.cb-menu-nav a span:before {
    content: attr(data-text);
    display: block;
    position: absolute;
    color: #000;
}
.cb-menu-nav a span:before {
    top: 0;
    transform: skewY(0);
    transform-origin: right bottom;
    transition: transform 2s cubic-bezier(0.19, 1, 0.22, 1);
}
.cb-menu-nav a span:after {
    top: 105%;
    transform: skewY(7deg);
    transform-origin: left top;
    transition: transform 2s cubic-bezier(0.19, 1, 0.22, 1);
}
.cb-menu-nav a:focus span,
.cb-menu-nav a:hover span {
    transform: translateY(-105%);
}
@media (pointer: coarse) {
    .cb-menu-nav a:focus span,
    .cb-menu-nav a:hover span {
        transform: none;
    }
}
.cb-menu-nav a:focus span:before,
.cb-menu-nav a:hover span:before {
    transform: skewY(7deg);
}
@media (pointer: coarse) {
    .cb-menu-nav a:focus span:before,
    .cb-menu-nav a:hover span:before {
        transform: none;
    }
}
.cb-menu-nav a:focus span:after,
.cb-menu-nav a:hover span:after {
    transform: skewY(0);
}
@media (pointer: coarse) {
    .cb-menu-nav a:focus span:after,
    .cb-menu-nav a:hover span:after {
        transform: none;
    }
}
.cb-menu-nav.-active a:before,
.cb-menu.-open .cb-menu-toggle button.cb-menu-nav a:before {
    transform: scaleX(1);
    transform-origin: left center;
}
.cb-menu-socials {
    display: inline-block;
    margin: -6px 0;
}
@media (min-width: 1600px) and (min-height: 700px) {
    .cb-menu-socials {
        margin: -6.5px 0;
    }
}
.cb-menu-social {
    position: relative;
    display: block;
    padding: 12px 0;
    font-size: 16px;
    line-height: 120%;
    letter-spacing: 0.00464286em;
    text-transform: capitalize;
    text-decoration: none;
}
@media (min-width: 1600px) and (min-height: 700px) {
    .cb-menu-social {
        padding: 13px 0;
    }
}
.cb-menu-social em {
    display: block;
    position: relative;
    overflow: hidden;
}
.cb-menu-social em span {
    display: block;
    transition: transform 1.2s cubic-bezier(0.19, 1, 0.22, 1);
    transform-origin: left center;
}
.cb-menu-social em span:after {
    content: attr(data-text);
    display: block;
    position: absolute;
    top: 130%;
    transform: skewY(10deg);
    transform-origin: left center;
    transition: transform 1.2s cubic-bezier(0.19, 1, 0.22, 1);
}
.cb-menu-social:focus em span,
.cb-menu-social:hover em span {
    transform: translateY(-130%) skewY(5deg);
}
@media (pointer: coarse) {
    .cb-menu-social:focus em span,
    .cb-menu-social:hover em span {
        transform: none;
    }
}
.cb-menu-social:focus em span:after,
.cb-menu-social:hover em span:after {
    transform: skewY(-5deg);
}
@media (pointer: coarse) {
    .cb-menu-social:focus em span:after,
    .cb-menu-social:hover em span:after {
        transform: none;
    }
}
.cb-menu-mail {
    display: block;
}
.cb-menu-mail a {
    position: relative;
    display: inline-block;
    padding: 0 0 5px 0;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.00464286em;
    text-decoration: none;
}
@media (min-width: 1600px) and (min-height: 700px) {
    .cb-menu-mail a {
        padding: 0 0 6px 0;
        font-size: 20px;
        line-height: 25px;
    }
}
.cb-menu-mail a:after,
.cb-menu-mail a:before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 1px;
    background: rgba(0, 0, 0, 0.3);
}
.cb-menu-mail a:after {
    background: #000;
    transform: scaleX(0);
    transition: transform 0.8s cubic-bezier(0.19, 1, 0.22, 1);
}
.cb-menu-mail a:focus:after,
.cb-menu-mail a:hover:after {
    transform: scaleX(1);
}
.cb-navbar.-inverse {
    color: #fff;
}
.cb-navbar-strip {
    position: absolute;
    top: 2.5rem;
    left: 2.5rem;
    right: 2.5rem;
    z-index: 99;
    pointer-events: none;
}
@media (min-width: 768px), (orientation: landscape) {
    .cb-navbar-strip {
        top: 3.9rem;
        left: 5rem;
        right: 5rem;
    }
}
.cb-navbar-grid {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.cb-navbar-grid-col {
    pointer-events: auto;
}
.cb-navbar-logo {
    position: relative;
    top: -1px;
    transition: opacity 0.8s;
}
.menu-open .cb-navbar-logo {
    opacity: 0;
}
.cb-navbar-logo a {
    color: currentColor;
}
.cb-navbar-logo svg {
    width: 88px;
    height: 19px;
}
@media (min-width: 768px), (orientation: landscape) {
    .cb-navbar-logo svg {
        width: 94px;
        height: 20px;
    }
}
.cb-navbar-links {
    display: inline-block;
    margin: 0 50px 0 0;
}
@media (min-width: 768px), (orientation: landscape) {
    .cb-navbar-links {
        margin: 0 60px 0 0;
    }
}
.cb-navbar-link {
    font-size: 18px;
    letter-spacing: -0.03em;
    text-transform: lowercase;
}
@media (min-width: 768px), (orientation: landscape) {
    .cb-navbar-link {
        font-size: 20px;
        letter-spacing: -0.03em;
    }
}
.cb-navbar-toggle {
    display: none;
}
@media (min-width: 768px), (orientation: landscape) {
    .cb-navbar-toggle {
        display: inline-block;
        margin: 0 50px 0 0;
        font-size: 20px;
        letter-spacing: -0.03em;
        text-transform: lowercase;
    }
}